  font-weight: 700;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  margin-bottom: 10px;
  background-color: transparent;
  color: #fdfdfd;
  border: 1px solid #fdfdfd;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 14px;
}
.sidebar .btn:hover,
.sidebar button:hover {
  background-color: #fdfdfd;
  color: #42a5c0;
}
