}
.sidebar .btn:focus,
.sidebar button:focus,
.main-content input[type="submit"]:focus,
.main-content btn:focus,
.main-content button:focus {
  opacity: 0.8;
}
input {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  padding: 10px;
