  border: 1px solid #fdfdfd;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  overflow: hidden;
}
.v-select .open-indicator:before {
  border-color: #fdfdfd;
}
.v-select .dropdown-toggle .clear {
  line-height: 0;
}
label,
input {
  display: block;
